import Carousel from 'react-multi-carousel';
import { mediaScreenMobile, mediaScreenTablet } from 'utils/constants';
import { carouselsData } from './const';

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: mediaScreenTablet },
    items: 2.7,
  },
  tablet: {
    breakpoint: { max: mediaScreenTablet, min: mediaScreenMobile },
    items: 2.3,
  },
  mobile: {
    breakpoint: { max: mediaScreenMobile, min: 325 },
    items: 1.1,
  },
};

function Carousel2() {
  return (
    <div className="carousel2">
      <Carousel
        itemClass="carousel2__item"
        arrows={false}
        responsive={responsive}
      >
        {carouselsData.map((c, i) => (
          <div key={i} className="carousel2__wrap">
            <div className="carousel2__spacing">
              <div className="carousel2__img">
                <img className="cover-img" src={c.url} alt="case study" />
              </div>
              <div className="carousel2__text">
                <span className="carousel2__stream">{c.stream}</span>
                <h5 className="carousel2__title line-clamp2">{c.title}</h5>
              </div>
            </div>
          </div>
        ))}
      </Carousel>
    </div>
  );
}

export default Carousel2;
